.Projects {
    padding: 16px;
    font-size: 0.9em;
}

.Projects > h2 {
    margin-bottom: 8px;
}

.Projects a {
    color: white;
    text-decoration: underline #999;
}

.Projects a:visited {
    color: white;
    text-decoration: underline #999;
}
.Project {
    background-color: #202020;
    margin-bottom: 16px;
    border-radius: 5px;
    padding-bottom: 10px;
}

.Project h3 {
    padding: 8px;
    background-color: #0c0c0c;
    border-radius: 5px 5px 0 0;
}

.Project-body > div  {
    margin: 10px 0 0 10px;
}