.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #090909;
}

.App > .container {
  flex: 1;
  display: flex;
  margin: 0 5vw;
  flex-direction: column;
}

.techs {
  margin: 12px 16px 0 16px;
  flex: 0;
  display: flex;
  flex-direction: row;
}

.techs > img {
  height: 35px;
  width: 40px;
  pointer-events: none;
  background-color: #111;
  border: 2px solid #333;
  border-radius: 8px;
  margin-right: 8px;
  padding: 6px;
}


@media (prefers-reduced-motion: no-preference) {

}

.App-header {
  padding: 8px;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 1;
  text-shadow: 2px 3px 0 rgb(150, 0, 0)
}

.App-link {
  color: #61dafb;
}

.App-main {
  flex: 1;
  background-color: #111;
  border-radius: 8px;
  margin-bottom: 16px;
  background-image: linear-gradient(135deg, #111111ff 50%, #11111100 50%), url("images/pattern.png");
  background-position-x: right;
  background-position-y: bottom;
  background-repeat: no-repeat;
  background-size: 80px 80px;
}